import { Link } from 'react-router-dom';
import { ProductCardDetails } from './ProductCardDetails';
import { Product } from '../../../helpers/types/Product';
import { PRODUCT_LINK } from '../../../helpers/constants/Links';
import { OldPrice } from '../OldPrice';
import { ProductButtons } from './ProductButtons';

type ProductCardProps = {
  product: Product
};

function format(mbValue: string) {
  return `${mbValue.split('MB')[0]} ${'MB'}`;
}

export const ProductCard = ({
  product,
}: ProductCardProps) => {
  const {
    imageUrl,
    name,
    price,
    discountedPrice,
    screen,
    capacity,
    ram,
    id,
  } = product;

  const newPrice = `$${discountedPrice}`;
  const displayedScren = screen.replace(' inches', '');
  const displayedCapacity = format(capacity);
  const displayedRam = format(ram);
  const cardFullPageLink = `${PRODUCT_LINK}/${id}`;

  return (
    <li className="product-card">
      <Link className="product-card__link" to={cardFullPageLink}>
        <img
          className="product-card__image"
          src={imageUrl}
          alt="Product"
        />

        <p className="product-card__name">{name}</p>

        <div className="product-card__prices">
          <h2 className="product-card__current-price">{newPrice}</h2>

          {discountedPrice < price && (
            <OldPrice>{price}</OldPrice>
          )}
        </div>

        <div className="product-card__details">
          {screen && (
            <ProductCardDetails name="Screen" value={displayedScren} />
          )}

          {capacity && (
            <ProductCardDetails name="Capacity" value={displayedCapacity} />
          )}

          {ram && (
            <ProductCardDetails name="Ram" value={displayedRam} />
          )}
        </div>
      </Link>

      <div className="product-card__buttons">
        <ProductButtons product={product} />
      </div>
    </li>
  );
};
